@import url(https://fonts.googleapis.com/css?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #CC0000;
  font-family: 'VT323', monospace; }

.App {
  text-align: center;
  padding: 5%; }
  .App .searchBar {
    width: 75%;
    height: 50px;
    margin: auto; }
    .App .searchBar .searchInput {
      width: 100%;
      height: 100%;
      border: solid 5px black;
      border-radius: 10px;
      font-size: 24px;
      font-family: 'VT323', monospace; }
    .App .searchBar #suggestList {
      background: white;
      margin-top: 0px;
      list-style: none;
      padding: 0px;
      margin: auto; }
      .App .searchBar #suggestList .suggestItem {
        text-transform: capitalize;
        text-align: left;
        font-size: 24px;
        border: 0.5px solid #b4b4ac;
        padding-top: 1%;
        padding-bottom: 1%; }
  .App .dashBoard {
    background: white;
    width: 25%;
    margin: auto;
    margin-top: 50px;
    border: solid 5px black;
    border-radius: 10px; }
    .App .dashBoard .pokeType {
      padding-left: 0px; }
    .App .dashBoard .typeItem {
      list-style: none;
      display: inline;
      padding-left: 2%;
      padding-right: 2%; }
    .App .dashBoard .typeTitle {
      color: white;
      text-transform: uppercase;
      padding: 1%;
      font-family: 'VT323', monospace;
      font-size: 1.5em; }
    .App .dashBoard .normal {
      background-color: #A8A77A; }
    .App .dashBoard .fire {
      background-color: #EE8130; }
    .App .dashBoard .water {
      background-color: #6390F0; }
    .App .dashBoard .electric {
      background-color: #F7D02C; }
    .App .dashBoard .grass {
      background-color: #7AC74C; }
    .App .dashBoard .ice {
      background-color: #96D9D6; }
    .App .dashBoard .fighting {
      background-color: #C22E28; }
    .App .dashBoard .poison {
      background-color: #A33EA1; }
    .App .dashBoard .ground {
      background-color: #E2BF65; }
    .App .dashBoard .flying {
      background-color: #A98FF3; }
    .App .dashBoard .psychic {
      background-color: #F95587; }
    .App .dashBoard .bug {
      background-color: #A6B91A; }
    .App .dashBoard .rock {
      background-color: #B6A136; }
    .App .dashBoard .ghost {
      background-color: #735797; }
    .App .dashBoard .dragon {
      background-color: #6F35FC; }
    .App .dashBoard .dark {
      background-color: #705746; }
    .App .dashBoard .steel {
      background-color: #B7B7CE; }
    .App .dashBoard .fairy {
      background-color: #D685AD; }

